#root {
    display: flex;
    text-align: center;
}

.App{
    margin: auto;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
}

a,
input{
    text-decoration: none;
    outline: none;
}

body{
    font-family: 'Inter', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #979797;;
}