
.header{
    display: flex;
    text-align: center;
    padding: 30px 15px;
    background: #686868;;
    border-radius: 5px;
    border: 1px solid #000;
}
.header__title{
    font-size: 35px;
    padding: 20px 0;
}
.header__block{
    border-radius: 5px;
    padding: 10px 15px;
    display: flex;
    gap: 30px;

}
.header__choice{

}
.header__subtext{
    margin-bottom: 15px;
    font-size: 20px;
    color: aliceblue;
}
.header__rates,
.header__input{
    font-weight: 600;
    padding: 18px 8px;
    text-align: center;
    font-size: 30px;
    border-radius: 5px;
    border: 2px solid #000;
    background: #cccccc;
}

.header__input:focus,
.header__rates:focus{
    box-shadow:
    0 -1px 1px rgba(33, 41, 34, 0.4),
    0 0  0 2px rgb(255,255,255),
    0.3em 0.3em 5px rgba(33, 41, 34, 0.4);
}

.main__date{
    font-size: 26px;
    text-align: left;
    font-weight: bold;
    color: #000;
    margin-bottom: 20px;
    border: 2px solid #000;
    background: #686868;
    padding: 20px 10px;
    border-radius: 5px;
}